.heder__container{
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding-top: 12px;
  padding-bottom: 12px;

  @media screen and (min-width: 768px) {
    padding-top: 20px;
    padding-bottom: 14px;
  }

  @media screen and (min-width: 1200px) {
    padding-top: 8px;
    padding-bottom: 8px;
  }
}

.header__logo {
  width: 40px;
  height: 37px;
  background-image: url('../images/header/logo-mob.png');
  background-repeat: no-repeat;
  background-size: contain;
  cursor: url('../images/header/vector.png'), pointer;

  @media screen and (min-width: 768px) {
    width: 48px;
    height: 44px;
    background-image: url('../images/header/logo-tabl.png');
  }

  @media screen and (min-width: 1200px) {
    width: 60px;
    height: 65px;
    background-image: url('../images/header/logo-des.png');
  }
}

.menu-toggle {
  display: flex;
  align-items: center;
  justify-content: center;

  min-height: 32px;
  min-width: 32px;
  padding: 5px;
  fill: #ffffff;
  background-color: transparent;

  border: none;
  outline: none;

  cursor: url('../images/header/vector.png'), pointer;

  @media screen and (min-width: 1200px) {
    display: none;
  }
}

.header__button {
  display: none;
  position: relative;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: calc(17 / 14);

  padding: 11px 50px 11px 35px;

  color: $secondary-color;
  background-color: $interactive-color;
  border-radius: 10px;
  border-color: transparent;

  cursor: url('../images/header/vector.png'), pointer;

  @media screen and (min-width: 768px) {
    display: block;
  }

  &:hover {
    background-color: $accent-color;
  }

  &:focus {
    background-color: $accent-color;
    color: $primary-color;
  }
}

.arrow {
  position: absolute;
  width: 12px;
  height: 12px;
  fill: currentColor;
  right: 35px;
  top: 50%;
  transform: translateY(-50%);
}

.header__nav {
  display: none;

  @media screen and (min-width: 1200px) {
    display: flex;
    gap: 28px;
  }
}

.nav__link {
  font-weight: 700;
  font-size: 16px;
  line-height: calc(20 / 16);
  text-align: center;
  color: $primary-color;

  cursor: url('../images/header/vector.png'), pointer;

  &:hover,
  &:focus {
    color: $interactive-color;
  }
}

.btn__wrap {
  display: flex;
  justify-content: center;
  gap: 32px;
  align-items: center;
}
